:root {
    --primaryColor  : #7a63f1;
    --secondaryColor: #ebe8fc;
    --textColor     : #a3a3a3;
    --greayColor    : #f4f4f4;
    --whiteColor    : #f5f4fa;
    --blackColor    : #434344;
}

.search {
    padding: 2rem 0;

    .title {
        color      : var(--blackColor);
        font-size  : 22px;
        padding    : 1rem 0;
        text-align : center;
        font-weight: 700;
        line-height: 1.7rem;


    }

    .searchDiv {
        gap                  : 0.5rem;
        grid-template-columns: repeat(2, 1fr);
        align-items          : center;

        input {
            border-radius: 10px;
            border       : 1px solid var(--secondaryColor);
            padding      : 5px 10px;
            outline      : none;
            width        : 100%;

            &:focus {
                border: none;
                border: 1px solid var(--primaryColor);
                color : var(--blackColor);

            }

        }

        .btn {
            justify-content: center;
            width          : 100%;
            align-self     : center;
            gap            : .3rem;

            .icon {
                font-size: 15px;
            }

            &:hover {
                background: var(--secondaryColor);
                color     : var(--blackColor);
            }
        }

    }

}


//mediaa query 

@media screen and (min-width:520px) {

    .search {
        .title {
            font-size: 25px;
            padding  : 2rem 0;
        }

        .searchDiv {
            input {
                padding    : 1rem 0;
                font-weight: 600;
                color      : var(--primaryColor);
                border-radius: 3px;
            }

            .btn {
                justify-content: center;
                width          : 100%;
                align-self     : center;
                gap            : .3rem;
                font-size      : 16px;


                .icon {
                    font-size: 15px;
                }

            }

        }

    }

}
/////
@media screen and (min-width:770px) {

    .search {
        width: 80%;
        margin: auto;
        

        .searchDiv {
            grid-template-columns: repeat(5, 1fr);
            
        }

    }

}
