:root {
    --primaryColor  : #7a63f1;
    --secondaryColor: #ebe8fc;
    --textColor     : #a3a3a3;
    --greayColor    : #f4f4f4;
    --whiteColor    : #f5f4fa;
    --blackColor    : #434344;
}

.review {
    .secContainer {
        .secHeading {
            justify-content: space-between;
            padding        : 1rem 0;

            .navBtns {
                gap: 0.5rem;

                .icon {
                    padding      : 3px 5px;
                    background   : var(--secondaryColor);
                    border-radius: 3px;
                    font-size    : 30px;
                    cursor       : pointer;

                    &:hover {
                        background-color: var(--primaryColor);
                        color           : var(--whiteColor);


                    }




                }
            }
        }


        .revContainer {
            gap: 1.5rem;

            .singleReview {
                transition: .3 ease-in-out;

                .imgDiv {
                    height       : 200px;
                    width        : 100%;
                    border-radius: 10px;
                    border       : 5px solid transparent;

                    img {
                        height    : 100%;
                        width     : 100%;
                        object-fit: contain;


                    }
                }

                .ReviewTitle {
                    color      : var(--blackColor);
                    font-size  : 16px;
                    padding-top: 10px;
                }

                .desc {
                    color    : var(--blackColor);
                    opacity  : .7;
                    font-size: 14px;
                }

                .revrDiv {
                    justify-content: space-between;
                    margin-top     : 1rem;
                    align-items    : flex-start;

                    .leftDiv {
                        gap: .8rem;

                        .revrImage {
                            border       : 2px solid var(--whiteColor);
                            height       : 40px;
                            width        : 40px;
                            overflow     : hidden;
                            border-radius: 50%;
                            box-shadow   : 0 4px 5px rgb(222, 222, 222);
                        }

                        .aboutReviewer {
                            .name {
                                color         : var(--blackColor);
                                font-weight   : 600;
                                padding-bottom: .5rem;
                                font-size     : 15px;

                            }

                            p {
                                color    : var(--textColor);
                                font-size: 14px;

                            }

                        }

                    }

                    .rightDiv {
                        gap: .3rem;

                        .icon {
                            color: rgb(255, 165, 0);
                        }

                        blockquote {
                            font-weight: 600;
                            font-size  : 14px;


                        }
                    }

                }


                &:hover {
                    transform: translateY(-7px);

                    .imgDiv {

                        border: 5px solid var(--primaryColor);
                    }
                }
            }
        }
    }
}

//mediaa query 

@media screen and (min-width:520px) {

    .review {
        .secContainer {
            .secHeading {
                margin-top: 1rem;

                .secTitle {
                    font-size: 23px;
                }

                .navBtns {
                    gap: 0.5rem;

                    .icon {
                        font-size: 40px;
                    }
                }
            }


            .revContainer {
                grid-template-columns: repeat(2, 1fr);

                .singleReview {
                    .ReviewTitle {
                        font-size: 18px;
                    }

                    .desc {
                        font-size: 16px;
                    }

                    .ReviewTitle {
                        color      : var(--blackColor);
                        font-size  : 16px;
                        padding-top: 10px;
                    }

                    .revrDiv {
                        .leftDiv {
                            .aboutReviewer {
                                .name {
                                    font-size: 17px;
                                }

                                p {
                                    font-size: 16px;
                                }

                            }

                        }

                        .rightDiv {
                            .icon {
                                font-size: 16px;
                            }

                            blockquote {
                                font-weight: 600;
                                font-size  : 17px;


                            }
                        }

                    }

                }

            }
        }
    }
}

@media screen and (min-width:940px) {

    .review {
        .secContainer {
            .secHeading {
                .secTitle {
                    font-size: 28px;
                }

                .navBtns {
                    gap: 1rem;
                }
            }


            .revContainer {
                grid-template-columns: repeat(3, 1fr);
            }
        }
    }
}