:root {
    --primaryColor  : #7a63f1;
    --secondaryColor:#ebe8fc;
    --textColor     : #a3a3a3;
    --greayColor    : #f4f4f4;
    --whiteColor    : #f5f4fa;
    --blackColor    : #434344;
}

.header {
    position       : fixed;
    display        : flex;
    align-items    : center;
    justify-content: space-between;
    width          : 100%;
    padding        : 1.5rem 2rem;
    box-shadow     : 0 4px 6px rgb(237, 237, 237);
    z-index        : 1000;

    .logoDiv {
        .logo {
            width: 80px;
        }
    }
    

    @media screen and (max-width:768px) {
        .navbar{
            position     : absolute;
            background   : var(--secondaryColor);
            width        : 80%;
            top          : -100rem;
            right        : 50%;
            left         : 50%;
            transform    : translate(-50%);
            border-radius: 10px;
            text-align   : center;
            padding      : 2rem;
            transition   : .3s ease;
            z-index      : 2000;

            .closeIcon {
                position     : absolute;
                bottom       : -15px;
                right        : 50%;
                left         : 50%;
                transform    : translate(-50%);
                background   : var(--secondaryColor);
                color        : var(--primaryColor);
                padding      : 3px;
                border-radius: 50%;
                font-size    : 35px;

                &:hover {
                    color: red;
                }

            }

        }

        .menu {
            display       : flex;
            align-items   : center;
            flex-direction: column;

            .listItem {
                padding      : .5rem 1rem;
                border-radius: 110px;
                cursor       : pointer;

                a {
                    font-size: 15px;
                }

                &:hover {
                    background: var(--primaryColor);
                    color     : var(--whiteColor);
                    transition: .3 ease;
                }

                &:hover a {
                    color: var(--whiteColor);
                }



            }
        }
    }
   

    .signUp {
        .text {
            padding      : 3px 10px;
            background   : var(--primaryColor);
            border-radius: 3px;
            color        : var(--whiteColor);
            cursor       : pointer;
            font-size    : 14px;

            &:hover {
                background: var(--secondaryColor);
                color     : var(--blackColor);
            }
        }

        .toggleNavbarIcon {
            background : none;
            color      : var(--primaryColor);
            font-weight: 600;
            font-size  : 25px;

        }

    }

    .showNavbar{
        top:7rem;
        transition:.5s ease ;
    }
}
.addBg{
  background: var(--secondaryColor);  
  box-shadow: none;
}

//media query 
@media screen and (min-width:520px) {
    .header{
        .logoDiv{
            img{
                width: 30px;

            }
        }
    }
    .signUp{
        .text{
            padding: 6px;
            font-size: 16px;
        }

        .toggleNavbarIcon{
            font-size: 30px;
        }
    }
}
@media screen and (min-width:770px) {
    .header{
        .closeIcon{
            display: none;
        }
        .menu{
            display: flex;
            flex-direction: row;
            .listItem{
                padding: .3rem 1rem;
                border-radius: 3px;

                a{
                    color: var(--textColor);
                    font-size:15px ;
                }
                &:hover{
                    background: var(--primaryColor);
                    color: var(--whiteColor);
                    transition: .3s ease;
                }
                &:hover a{
                    color: var(--whiteColor);
                
                }
            }
        }
        .signUp{
            .text{
                padding: 6px 10px;
                font-size: 16px;
            }
    
            .toggleNavbarIcon{
                display: none;
            }
        }
      
    }

}

@media screen and (min-width:1064px){
    .header{
        padding: 2rem 7rem;
       .navbar{
        .menu{
            .listItem{
                a{
                   font-size: 15px; 
                }
            }
        }

       }
    }
    .signUp{
        .text{
            
            font-size: 18px;
        }

     
    }
}

@media screen and (min-width:1400px){
    .header{
        .navbar{
         .menu{
             .listItem{
              margin: 0 .8rem;
             }
         }
 
        }
     }

}