:root {
    --primaryColor  : #7a63f1;
    --secondaryColor: #ebe8fc;
    --textColor     : #a3a3a3;
    --greayColor    : #f4f4f4;
    --whiteColor    : #f5f4fa;
    --blackColor    : #434344;
}

.auction {
    .secContainer {
        .secHeading {
            justify-content: space-between;
            padding        : 1rem 0;

            .navBtns {
                gap: 0.5rem;

                .icon {
                    padding      : 3px 5px;
                    background   : var(--secondaryColor);
                    border-radius: 3px;
                    font-size    : 30px;
                    cursor       : pointer;

                    &:hover {
                        background-color: var(--primaryColor);
                        color           : var(--whiteColor);


                    }




                }
            }
        }

        .carContainer {
            gap: 2rem;

            .singleCar {
                border-radius: 10px;
                padding      : 1rem;
                text-align   : left;
                transition   : .3 ease-in-out;

                .imgDiv {
                    height       : 200px;
                    width        : 100%;
                    overflow     : hidden;
                    border-radius: 10px;
                    border       : 5px solid transparent;

                    img {
                        height    : 100%;
                        width     : 100%;
                        object-fit: contain;

                    }
                }

                .carTitle {
                    color      : var(--blackColor);
                    font-size  : 15px;
                    padding-top: 10px;
                }

                .miles {
                    color    : var(--blackColor);
                    opacity  : .7;
                    font-size: 14px;
                }

                .AWD {
                    padding    : 1rem;
                    color      : var(--blackColor);
                    font-weight: 600;
                    font-size  : 15px;
                }

                .price_buyBtn {
                    gap: 1rem;

                    span {

                        padding      : .5rem 1rem;
                        border-radius: 3px;
                        font-weight  : 600;
                    }

                    .price {
                        background: var(--secondaryColor);
                        color     : var(--primaryColor);
                    }

                    .buyBtn {
                        background: var(--primaryColor);
                        color     : var(--whiteColor);
                        cursor    : pointer;

                        &:hover {
                            background-color: var(--secondaryColor);
                            color           : var(--textColor);


                        }

                    }

                }

                &:hover {
                    transform : translateY(-7px);
                    padding   : 1rem;
                    background: var(--secondaryColor);

                    .price_buyBtn {
                        span {
                            border: 1px solid var(--primaryColor);
                        }
                    }
                }
            }

            .singleCarActive {
                padding   : 1rem;
                background: var(--secondaryColor);

                .price_buyBtn {
                    span {
                        border: 1px solid var(--primaryColor);
                    }
                }
            }
        }
    }
}


//mediaa query 

@media screen and (min-width:520px) {
    .auction {
        .secContainer {
            .secHeading {
                margin-top: 1rem;

                .secTitle {
                    font-size: 23px;
                }

                .navBtns {
                    gap: 0.5rem;

                    .icon {
                        font-size: 40px;
                    }
                }
            }

            .carContainer {
                grid-template-columns: repeat(2, 1fr);

                .singleCar {
                    .carTitle {
                        font-size: 18px;
                    }

                    .miles,
                    .AWD {

                        font-size: 16px;
                    }

                    .price_buyBtn {
                        font-weight: 600;
                    }

                }

            }
        }
    }

}
@media screen and (min-width:940px) {
    .auction {
        .secContainer {
            .secHeading {
                .secTitle {
                    font-size: 28px;
                }

                .navBtns {
                    gap: 1rem;
                }
            }

            .carContainer {
                grid-template-columns: repeat(3, 1fr);
            }
        }
    }

}